@use '../../styles/partials/mixins' as *;
@use '../../styles/partials/variables' as *;
@use '../../styles/partials/colors' as *;
@use '../../styles/partials/typography' as *;

.comment {

    &__container {
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 0 $spacing-mob;
        margin-bottom: 0;
        width: 100%;
        
        @include tablet {
            padding: 0 $margin-tablet;
        }

        @include desktop {
            padding: 0;
        }
    }

    &__wrapper {
        display: flex;
        justify-content: flex-start;
        width: 100%;
        @include bottom-border;
        margin-top: $spacing-mob;

        @include desktop {
            padding: 0 $margin-tablet;
        }
    }

    &__avatar-delete-wrapper {
        display: flex;
        flex-direction: column;
    }

    &__avatar {
        background-color: $color-mercury;
        width: $avatar-mob;
        height: $avatar-mob;
        border-radius: 50%;

        @include tablet {
            width: $avatar-tablet;
            height: $avatar-tablet;
        }
    }

    &__delete {
        border-radius: 4px;
        height: 1rem;
        margin-top: 1rem;
        box-sizing: content-box;

        &:hover {
            border: solid 2px $color-error;
        }
    }

    &__area {
        width: 100%;
        padding-left: $spacing-mob;
    }

    &__nameDate {
        display: flex;
        justify-content: space-between;
    }

    &__name {
        font-weight: 600;
        margin-top: 0;
        font-size: $font-size-med;
    }

    &__date {
        color: $color-silver;
        margin-top: 0;
        font-size: $font-size-med;
    }

    &__text {
        margin-top: 0;
        font-size: $font-size-med;
    }
}