@use 'variables' as *;
@use 'typography' as *;
@use 'colors' as *;
@use 'mixins' as *;

* {
    box-sizing: border-box;
}

html {
    font-size: 16px;
}

body {
    font-family: $font-stack-avenir;
    color: $color-bf-black;
    @include margin-reset;
}
// prevent browsers default outlining of input boxes
input:focus, textarea:focus, select:focus{
    outline: none;
}

input[type="text"] {
    font-size: 1rem;
    font-family: $font-stack-avenir;
}

a {
    text-decoration: none;
}