@use '../../styles/partials/variables' as *;
@use '../../styles/partials/colors' as *;
@use '../../styles/partials/mixins' as *;

.video-details {
    padding: $spacing-mob;

    @include tablet {
        padding: $margin-tablet;
    }

    @include desktop {
       padding: $margin-tablet 0;
    } 

    &__title {
        font-size: 32px;
        line-height: 32px;
        font-weight: 700;
        padding: $spacing-x-small 0 $spacing-mob;
        @include bottom-border;

        @include tablet {
            font-size: 48px;
            border: none;
            line-height: 44px;
        }

        @include desktop {
            padding-left: $margin-tablet;
            padding-right: $margin-tablet;
        }
    }

    &__info-wrapper {
        display: flex;
        @include bottom-border;

        @include tablet {
            justify-content: space-between;
        }

        @include desktop {
            padding: 0 $margin-tablet;
        }
    }

    &__channel-date {
        width: 50%;

        @include tablet {
            display: flex;
            width: auto;
        }
    }
    
    &__views-likes {
        @extend .video-details__channel-date;
    }

    &__date {
        color: $color-silver;
        font-size: 18px;
        line-height: 22px;
    }
    
    &__channel {
        @extend .video-details__date;
        color: $color-bf-black;
        font-weight: 700;
        margin-right: 2rem;
    }

    &__views {
        @extend .video-details__date;
        margin-right: 2rem;
    }

    &__likes {
        @extend .video-details__date;

        &:hover {
            cursor: pointer;
            color: red;
            text-shadow: 2px 2px $color-silver;
            border-bottom: 2px solid $color-silver;
        }
    }

    &__description {
        font-size: 18px;
        line-height: 22px;
        margin-bottom: 0;

        @include tablet {
            padding-top: $spacing-tablet;
            font-size: 20px;
            line-height: 28px;
        }

        @include desktop {
            padding-left: $margin-tablet;
            padding-right: $margin-tablet;
        }
    }
}