@use '../../styles/partials/variables' as *;
@use '../../styles/partials/colors' as *;
@use '../../styles/partials/mixins' as *;

.next-video {

    &__section {
        @include desktop {
            border-left: solid 1px $color-mercury;
        }
    }

    &__heading {
        padding: $spacing-mob;
        margin: 0;
        color: $color-silver;

        @include tablet {
            padding-left: $margin-tablet;
            padding-right: $margin-tablet;
        }
    }

    &__wrapper {
        display: flex;
        padding: 0 $spacing-mob;
        margin-bottom: $spacing-mob;
        
        @include tablet {
            padding: 0 $margin-tablet;
        }
    }

    &__image-container {
        width: 8rem;
        height: 5rem;
    }

    &__thumbnail {
        width: 100%;
        height: 100%;
        border-radius: 4px;
        object-fit: cover;
        overflow: hidden;
    }

    &__text-wrapper {
        width: 75%;;
        padding-left: $spacing-mob;
        color: $color-bf-black;
    }
    
    &__title {
        font-weight: 700;
        margin-top: 0;
    }

    &__name {
        margin-bottom: 0;
    }
}