@use '../../styles/partials/colors' as *;
@use '../../styles/partials/mixins' as *;

.video {
    width: 100%;
    margin-top: 1rem;
    background-color: black;
    display: flex;
    justify-content: center;

    @include tablet {
        margin-top: 0;
    }

    &__player {
        width: 100vw;
        object-fit: cover;
        // 16:9 aspect ratio - Height = 0.5625 * width
        max-height: 56.25vw;

        @include tablet {
            width: 90%;
            height: calc(0.9 * 56.25vw);
        }

        @include desktop {
            width: 70%;
            height: calc(0.7 * 56.25vw);
        }
    }
}