@use 'variables' as *;
@use 'colors' as *;

@mixin tablet {
    @media screen and (min-width: $tablet-breakpoint) {
      @content;
    }
}
  
@mixin tablet-1024 {
    @media screen and (min-width: $tablet-1024-breakpoint) {
      @content;
    }
}
  
@mixin desktop {
    @media screen and (min-width: $desktop-breakpoint) {
      @content;
    }
}

@mixin padding-reset {
  padding: 0;
}

@mixin margin-reset {
  margin: 0;
}

@mixin flex-align-center {
  display: flex;
  align-items: center;
}

@mixin flex-justify-center {
  display: flex;
  justify-content: center;
}

@mixin flex-justify-align-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

@mixin bottom-border {
  border-bottom: 1px solid $color-mercury;
}