@use '../../styles/partials/colors' as *;
@use '../../styles/partials/mixins' as *;
@use '../../styles/partials/typography' as *;
@use '../../styles/partials/variables' as *;

.CTAButton {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 100%;
    height: $button-height;
    font-size: 1rem;
    border-radius: 4px;
    background-color: $color-bf-blue;
    border: none;

    &:hover {
        cursor: pointer;
    }

    @include tablet {
        width: 10rem;
        margin: 0;
    }
    
    &__icon {
        position: absolute;
        top: calc(50% - 0.675rem);
        left: 1rem;
        height: 1.25rem;
    }

    &__text {
        color: white;
        text-transform: uppercase;
        text-align: center;
        font-weight: 700;
    }
}

