@use '../../styles/partials/mixins' as *;
@use '../../styles/partials/variables' as *;
@use '../../styles/partials/colors' as *;
@use '../../styles/partials/typography' as *;

.comment-form {
    display: flex;
    flex-direction: column;
    padding-left: $spacing-mob;
    width: 100%; 

    @include tablet {
        flex-direction: row;
        align-items: flex-end;
    }

    &__section {
        padding: 0 $spacing-mob;

        @include flex-align-center;
        flex-direction: column;
        justify-content: flex-start;
    
        @include tablet {
            padding: 0 $margin-tablet 1rem;
        }

        @include desktop {
            @include bottom-border;
        }
    }

    &__heading {
        margin: 0;
        padding: $spacing-x-small 0 $spacing-mob;
        align-self: flex-start;
    }

    &__container {
        display: flex;
        justify-content: flex-start;
        width: 100%;
        padding-bottom: $spacing-mob;
        @include bottom-border;

        @include tablet {
            width: 100%;
            padding-bottom: $spacing-tablet;
        }

        @include desktop {
            border: none;
        }
    }

    &__avatar {
        height: $avatar-mob;

        @include tablet {
            height: $avatar-tablet;
        }
    }

    &__avatar-wrapper {
        border-radius: 50%;
        width: $avatar-mob;
        height: $avatar-mob;
        overflow: hidden;
        margin-top: 2.75rem;

        @include tablet {
            width: $avatar-tablet;
            height: $avatar-tablet;
            margin-top: 2.25rem;
        }
    }

    &__label-and-textarea {
        flex-grow: 1;
    }

    &__subheading {
        color: $color-silver;
        font-weight: 600;
        text-transform: uppercase;
        margin-bottom: 0.25rem;
    }

    &__comment {
        width: 100%;
        height: 8rem;
        background-color: $color-alabaster;
        border: $color-mercury solid 2px;
        border-radius: $border-radius;
        font-size: $font-size-med;
        padding-left: 1rem;
        padding-top: 1rem;
        font-family: $font-stack-avenir;
        margin-bottom: $spacing-mob;

        &:focus {
            border: $color-bf-blue solid 2px;
        }

        &::placeholder {
            font-size: $font-size-med;
            color: $color-bf-black;
        }

        &--error {
            border: solid 2px $color-error;
        }

        @include tablet {
            height: 40px;
            padding: 0.25rem 0.5rem 0;
            margin-bottom: 0;
        }
    }

    &__button-wrapper {
        @include tablet {
            padding-bottom: 3px;
            margin-left: $button-spacing;
        }
    }
}