@use '../../styles/partials/variables' as *;
@use '../../styles/partials/colors' as *;
@use '../../styles/partials/mixins' as *;
@use '../../styles/partials/typography' as *;

.video-upload {
    &__container {
        border-top: solid 1px $color-mercury;
        padding: 0 $spacing-mob;
        
        @include tablet {
            padding: 0 $margin-tablet;
        }

        @include desktop {
            padding: 0 $margin-desktop;
        }
    }

    &__title {
        @include tablet {
            font-size: 48px;
            line-height: 44px;
            @include bottom-border;
            padding-bottom: $spacing-tablet;
        }
    }

    &__thumbnail-form-wrapper {
        @include desktop {
            display: flex;
        }
    }

    &__subheading {
        color: $color-silver;
        font-weight: 600;
        text-transform: uppercase;
        // margin-top: $spacing-tablet;
        margin-bottom: 0.25rem;
    }

    &__thumbnail-wrapper {
        @include desktop {
            width: 30%;
        }
    }

    &__thumbnail {
        width: 100%;
        border-radius: 4px;
        margin-bottom: $spacing-mob;

        @include tablet {
            width: 50%;
        }

        @include desktop {
            width: 100%;
        }
    }

    &__title-input {
        width: 100%;
        height: 40px;
        padding: 0 0.25rem;
        background-color: $color-alabaster;
        border: $color-mercury solid 2px;
        border-radius: $border-radius;
        font-size: $font-size-med;
        padding-left: 1rem;
        font-family: $font-stack-avenir;
        margin-bottom: $spacing-mob;

        &:focus {
            border: $color-bf-blue solid 2px;
        }

        &::placeholder {
            font-size: $font-size-med;
            color: $color-bf-black;
        }

        &--error {
            border: solid 2px $color-error;
        }
    }

    &__description {
        width: 100%;
        height: 8rem;
        background-color: $color-alabaster;
        border: $color-mercury solid 2px;
        border-radius: $border-radius;
        font-size: $font-size-med;
        padding-left: 1rem;
        padding-top: 1rem;
        font-family: $font-stack-avenir;
        margin-bottom: $spacing-mob;

        &:focus {
            border: $color-bf-blue solid 2px;
        }

        &::placeholder {
            font-size: $font-size-med;
            color: $color-bf-black;
        }

        &--error {
            border: solid 2px $color-error;
        }
    }

    &__links-wrapper {
        padding-bottom: $spacing-mob;
        @include tablet {
            display: flex;
            flex-direction: row-reverse;
            padding-bottom: $spacing-tablet;
        }
    }

    &__cancel {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 40px;
        color: $color-bf-blue;
        text-transform: uppercase;
        text-align: center;
        font-weight: 700;
        margin: $spacing-mob 0;

        @include tablet {
            width: 10rem;
            margin: 0;
            margin-right: $button-spacing;
        }
    }

    &__form {
        @include tablet {
            margin-bottom: $spacing-tablet;
            @include bottom-border;
        }

        @include desktop {
            flex-grow: 1;
            margin-left: $spacing-tablet;
            margin-top: $spacing-tablet;
            border: none;
        }
    }
}