@use '../../styles/partials/colors' as *;
@use '../../styles/partials/mixins' as *;
@use '../../styles/partials/typography' as *;
@use '../../styles/partials/variables' as *;

.header {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    padding: 0 $spacing-mob;
    margin-bottom: $spacing-mob;

    @include tablet {
       flex-direction: row;
       justify-content: space-between;
       padding: 0 $margin-tablet;
       margin-bottom: 0;
    }

    @include desktop {
        padding: 0 $margin-desktop;
    }
    
    &__logo {
        width: 7.5rem;
        margin: $spacing-mob;

        @include tablet {
            margin: 0;
        }
    }
    
    &__right {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;

        @include tablet {
            flex-direction: row;
            justify-content: flex-end;
         }
    }

    &__search-avatar-wrapper {
        display: flex;
        width: 100%;
        margin: 0 0 $spacing-mob;
        justify-content: space-between;
        
        @include tablet {
            flex-direction: row-reverse;
            width: 20rem;
            margin: $spacing-tablet 0;
        }
    }

    &__search-wrapper {
        box-sizing: border-box;
        display: flex;
        border: $color-mercury solid 2px;
        border-radius: 4px;
        margin-right: $spacing-mob;
        height: $form-height-mob;
        background-color: $color-alabaster;
        width: calc(100% - 4rem);

        @include tablet {
            width: 90%;
            margin: 0;
            height: $form-height-tablet;
        }
    }

    &__search {
        border: none;
        width: 90%;
        background-color: $color-alabaster;
        margin-right: $form-padding-mob;

        &:focus {
            border: $color-bf-blue solid 2px;
        }

        &::placeholder {
            font-size: 1rem;
            color: $color-bf-black;
            margin: $form-padding-tablet;
            margin-left: 0;
        }
    }

    &__search-icon {
        height: 50%;
        margin: $spacing-x-small;
    }

    &__avatar {
        height: $avatar-mob;

        @include tablet {
            height: $avatar-tablet;
        }
    }

    &__avatar-wrapper-a {
        border-radius: 50%;
        width: $avatar-mob;
        height: $avatar-mob;
        overflow: hidden;

        @include tablet {
            display: none;
        }
    }

    &__avatar-wrapper-b {
        @extend .header__avatar-wrapper-a;
        display: none;

        @include tablet {
            display: flex;
            width: $avatar-tablet;
            height: $avatar-tablet;
        }
    }

    &__button-Link {
        width: 100%;

        @include tablet {
            width: 10rem;
            margin-right: $spacing-tablet;
            margin-left: $button-spacing;
        }
    }
}